@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400;500&display=swap');

@font-face {
  font-family: 'Autopia-Regular';
  src: url('./assets/Autopia-Regular.woff');
}

/**BASE SETTINGS**/

:root {
  --body: #1F3304;
  --body-light: hsla(86, 85%, 11%, 0.5);
}

.App {
  height: 100vh;
  display: flex;
  align-items: flex-end;
  background-color: rgb(103, 104, 99);
  background: radial-gradient(circle, #B7D0D7 0%, #F1DEB7 100%), 
  radial-gradient(ellipse at bottom, #FCC482 0%, rgb(80, 194, 226) 100%), 
  linear-gradient(121.28deg, #FFEAB6 0%, rgb(86, 0, 0) 100%), 
  linear-gradient(229.79deg, #7534FF 0%, #248900 94.19%), 
  radial-gradient(56.26% 101.79% at 50% 0%, #8F00FF 0%, #493500 100%), 
  linear-gradient(65.05deg, #D4DFE3 0%, #FFD600 100%);
  background: -moz-radial-gradient(circle, #B7D0D7 0%, #F1DEB7 100%), 
  -moz-radial-gradient(ellipse at bottom, #FCC482 0%, rgb(80, 194, 226) 100%), 
  -moz-linear-gradient(121.28deg, #FFEAB6 0%, rgb(86, 0, 0) 100%), 
  -moz-linear-gradient(229.79deg, #7534FF 0%, #248900 94.19%), 
  -moz-radial-gradient(56.26% 101.79% at 50% 0%, #8F00FF 0%, #493500 100%), 
  -moz-linear-gradient(65.05deg, #D4DFE3 0%, #FFD600 100%);
  background-blend-mode: overlay, screen, color-burn, hard-light, screen, normal;
  position: relative;
}

body {
  color: var(--body);
  font-size: 62.5%;
  font-family: 'Work Sans', sans-serif;
  font-weight: 100;
  cursor: crosshair;
  transition: all .3s;
}

h1 {
  font-size: 2rem;
  font-weight: 200;
}

h3 {
  font-family: 'Work Sans', sans-serif;
  font-weight: 300;
  font-size: 1rem;
}

.heavy {
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
}

.heavy:hover {
  font-style: italic;
}

h4 {
  font-family: 'Work Sans', sans-serif;
  font-weight: 400;
  font-size: 1rem;
}

p {
  font-size: .8rem;
  font-weight: 400;
  line-height: 150%;
}

a {
  text-decoration: none;
  color: var(--body);
}

.close {
  width: 100%;
  height: 1rem;
  display: flex;
  justify-content: flex-start;
}

/**LAYOUT**/
.page { /*wrapping a page w/ this class will disable the canvas*/
  position: relative;
}

.wrapper--menu {
  height: 100vh;
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.wrapper--right {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex: 0 1 80%;
  overflow: scroll;
  background-image: radial-gradient(ellipse, rgba(183, 208, 215, 0.658) 0%, transparent 50%);
}

.wrapper--page {
  margin: 0 5rem;
  padding: 5rem;
}

/**NAVIGATION MENU**/
.menu {
  width: 75%;
  height: 20vh;
  padding: 1rem;
  margin: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: 5px solid rgba(255, 255, 255, 0.4);
  border-radius: 1rem;
  box-shadow: 20px 20px 60px #e7f0db, 
  -20px -20px 60px #fffff2;
}

.menu--item {
  display: flex;
  align-items: baseline;
  padding-bottom: 0.5rem;
}

.menu--item:hover {
  font-style: italic;
}

.menu--item:hover > .menu--empty {
  background-color: var(--body);
}

.menu--empty {
  height: .75rem;
  width: .75rem;
  border: 1px solid var(--body);
  border-radius: 50%;
  margin-right: 1rem;
}

.social--row {
  width: 80%;
  display: flex;
  justify-content: space-between;
}
.social {
  font-size: 1.5rem;
  cursor: pointer;
}

.social__icon{
  height: 1rem;
}

.clear {
  cursor: pointer;
  align-self: flex-end;
}

/*****TITLE CARD******/
.card {
  width: 40%;
  height: 35vh;
  border-radius: 1rem;
  margin: 3rem;
  padding: 3rem 5rem;
  border: 5px solid rgba(255, 255, 255, 0.4);
  box-shadow:  20px 20px 60px #e7f0db, 
  -20px -20px 60px #fffff2;
  overflow: hidden;
  align-self: flex-end;
}

.card--title {
width: 90%;
border-top: 1px solid var(--body);
}

.card--text:not(:first-child) {
  padding-top: 1rem;
}

.card--line{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: 90%;
  padding-bottom: 1rem;
}

.card--name {
  display: flex;
  height: 1rem;
  width: 50%;
  border-bottom: 1px solid var(--body);
  margin-right: 1rem;
}

.card--date {
  display: flex;
  height: 1rem;
  width: 20%;
  border-bottom: 1px solid var(--body);
}

.card--signature {
  display: flex;
  height: 1rem;
  width: 85%;
  border-bottom: 1px solid var(--body);
}

/***PROJECT PAGE****/
.project--card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 65%;
  max-width: 40rem;
  border: 1px solid var(--body);
  padding: 1rem;
  margin: 3rem 1rem;
  border-radius: 1em;
  border: 5px solid rgba(255, 255, 255, 0.4);
  box-shadow:  7px 7px 14px #efece1;
}

.project--title {
  padding: 0 1rem;
  max-width: 85%;
  align-self: flex-start;
}

.project--description {
  max-width: 85%;
  padding: 0 1rem;
  text-align: left;
}

.project--img-gallery {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.project--img { /*four imgs in gallery*/
  max-width: 25%;
  height: auto;
  max-height: 5rem;
}

.project--img__large { /*one img in gallery*/
  max-width: 85%;
  height: auto;
  border: 5px solid rgba(255, 255, 255, 0.4);
}

/* CANVAS -- OFF VS ON */
.canvas--inactive {
  position: absolute;
  pointer-events: none;
}

.canvas--active {
  position: absolute;
  pointer-events: auto;
  cursor: url('./assets/pencil.png'), auto;
}

/* Cursor styling, not used */
.cursor {
  width: 8rem;
  height: 8rem;
  background-image: radial-gradient(rgba(0, 2, 110, 0.479) 10%, transparent 75%);
  border-radius: 100%;
  position: fixed;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 10;
  mix-blend-mode: difference;
}